"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { useAuthContext } from "components/providers/auth-provider"
import Loading from "components/ui/loading"

export default function ProtectedRoute({ children }: { children: React.ReactNode }) {
  const { user, loading } = useAuthContext()
  const router = useRouter()
  const [hasChecked, setHasChecked] = useState(false)

  useEffect(() => {
    if (!loading && !hasChecked) {
      setHasChecked(true)
      
      if (!user) {
        console.log('ProtectedRoute: No user found, redirecting to welcome')
        router.push("/welcome")
      } else {
        console.log('ProtectedRoute: User authenticated:', user.email)
      }
    }
  }, [loading, user, router, hasChecked])

  // Show loading while checking auth or during initial load
  if (loading || !hasChecked) {
    return <Loading />
  }

  // If no user after checking, show nothing (redirect is happening)
  if (!user) {
    return null
  }

  // User is authenticated, show protected content
  return <>{children}</>
}
