"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "components/ui/button"
import { Card } from "components/ui/card"
import { 
  Home,
  Map,
  Calendar,
  User,
  Settings,
  CreditCard,
  Heart,
  HelpCircle,
  LogOut
} from "lucide-react"
import { useAuthContext } from "components/providers/auth-provider"
import { toast } from "sonner"
import ProtectedRoute from "components/auth/protected-route"

// Try to import Firebase, fallback to mock if it fails
let db: any
let doc: any
let getDoc: any

try {
  const firebase = require('lib/firebase')
  db = firebase.db
  const firestore = require('firebase/firestore')
  doc = firestore.doc
  getDoc = firestore.getDoc
} catch (error) {
  console.warn('Firestore not available, using mock:', error)
  const mockAuth = require('lib/auth-mock')
  db = mockAuth.mockDb
  doc = (collection: string, id: string) => db.doc(`${collection}/${id}`)
  getDoc = async (docRef: any) => {
    return docRef.get()
  }
}

interface UserProfile {
  fullName: string
  email: string
  createdAt: string
}

const menuItems = [
  {
    icon: Settings,
    label: "Configurações",
    path: "/configuracoes"
  },
  {
    icon: CreditCard,
    label: "Pagamentos",
    path: "/pagamentos"
  },
  {
    icon: Heart,
    label: "Favoritos",
    path: "/favoritos"
  },
  {
    icon: HelpCircle,
    label: "Ajuda",
    path: "/ajuda"
  }
]

export default function PerfilPage() {
  const router = useRouter()
  const { user, logout } = useAuthContext()
  const [profile, setProfile] = useState<UserProfile | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchProfile = async () => {
      if (!user) return

      try {
        const docRef = doc("users", user.uid)
        const docSnap = await getDoc(docRef)
        
        if (docSnap.exists && docSnap.exists()) {
          setProfile(docSnap.data() as UserProfile)
        } else {
          // Fallback to user data from auth
          setProfile({
            fullName: user.displayName || user.email?.split('@')[0] || 'Usuário',
            email: user.email || '',
            createdAt: new Date().toISOString()
          })
        }
      } catch (error) {
        console.error("Error fetching profile:", error)
        // Fallback to user data from auth
        setProfile({
          fullName: user.displayName || user.email?.split('@')[0] || 'Usuário',
          email: user.email || '',
          createdAt: new Date().toISOString()
        })
        toast.error("Erro ao carregar perfil")
      } finally {
        setLoading(false)
      }
    }

    fetchProfile()
  }, [user])

  const handleLogout = async () => {
    try {
      await logout()
      router.push("/welcome")
    } catch (error) {
      toast.error("Erro ao sair")
    }
  }

  return (
    <ProtectedRoute>
      <div className="min-h-screen bg-gray-50 pb-20">
        {/* Header */}
        <div className="bg-red-600 px-4 pt-12 pb-8">
          <div className="flex items-center gap-4">
            <div className="w-20 h-20 bg-white rounded-full flex items-center justify-center">
              <User className="w-10 h-10 text-gray-400" />
            </div>
            <div>
              {loading ? (
                <div className="space-y-2">
                  <div className="h-7 w-40 bg-white/20 animate-pulse rounded"></div>
                  <div className="h-5 w-32 bg-white/20 animate-pulse rounded"></div>
                </div>
              ) : (
                <>
                  <h1 className="text-2xl font-semibold text-white">
                    {profile?.fullName || "Usuário"}
                  </h1>
                  <p className="text-white/80">{profile?.email}</p>
                </>
              )}
            </div>
          </div>
        </div>

        {/* Stats Cards */}
        <div className="px-4 -mt-6">
          <div className="grid grid-cols-2 gap-4">
            <Card className="p-4 bg-white">
              <p className="text-gray-600 text-sm">Roteiros</p>
              {loading ? (
                <div className="h-8 w-16 bg-gray-200 animate-pulse rounded mt-1"></div>
              ) : (
                <p className="text-2xl font-semibold">0</p>
              )}
            </Card>
            <Card className="p-4 bg-white">
              <p className="text-gray-600 text-sm">Cupons Usados</p>
              {loading ? (
                <div className="h-8 w-16 bg-gray-200 animate-pulse rounded mt-1"></div>
              ) : (
                <p className="text-2xl font-semibold">0</p>
              )}
            </Card>
          </div>
        </div>

        {/* Menu Items */}
        <div className="px-4 mt-6">
          <div className="space-y-2">
            {menuItems.map((item) => {
              const Icon = item.icon
              return (
                <button
                  key={item.label}
                  onClick={() => router.push(item.path)}
                  className="w-full p-4 bg-white rounded-lg flex items-center gap-4 hover:bg-gray-50 transition-colors"
                >
                  <Icon className="w-5 h-5 text-gray-600" />
                  <span className="font-medium">{item.label}</span>
                </button>
              )
            })}
          </div>

          <Button
            variant="outline"
            className="w-full mt-6 text-red-600 border-red-600 hover:bg-red-50"
            onClick={handleLogout}
            disabled={loading}
          >
            <LogOut className="w-4 h-4 mr-2" />
            Sair
          </Button>
        </div>

        {/* Bottom Navigation */}
        <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 p-4">
          <div className="flex justify-around">
            <button 
              onClick={() => router.push("/home")}
              className="flex flex-col items-center text-gray-400"
            >
              <Home className="w-6 h-6" />
              <span className="text-xs mt-1">Início</span>
            </button>
            <button 
              onClick={() => router.push("/mapa")}
              className="flex flex-col items-center text-gray-400"
            >
              <Map className="w-6 h-6" />
              <span className="text-xs mt-1">Mapa</span>
            </button>
            <button 
              onClick={() => router.push("/resultados")}
              className="flex flex-col items-center text-gray-400"
            >
              <Calendar className="w-6 h-6" />
              <span className="text-xs mt-1">Roteiro</span>
            </button>
            <button className="flex flex-col items-center text-red-600">
              <User className="w-6 h-6" />
              <span className="text-xs mt-1">Perfil</span>
            </button>
          </div>
        </div>
      </div>
    </ProtectedRoute>
  )
}
