// Mock authentication for development
export interface MockUser {
  uid: string
  email: string
  displayName?: string
}

class MockAuth {
  private currentUser: MockUser | null = null
  private listeners: ((user: MockUser | null) => void)[] = []

  async signInWithEmailAndPassword(email: string, password: string): Promise<{ user: MockUser }> {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 1000))
    
    // Simple validation
    if (!email || !password) {
      throw new Error('Email and password are required')
    }
    
    if (password.length < 6) {
      throw new Error('Password should be at least 6 characters')
    }

    const user: MockUser = {
      uid: `mock-${Date.now()}`,
      email,
      displayName: email.split('@')[0]
    }

    this.currentUser = user
    
    // Store in localStorage for persistence
    localStorage.setItem('mockUser', JSON.stringify(user))
    
    // Notify listeners immediately
    this.notifyListeners()
    
    return { user }
  }

  async createUserWithEmailAndPassword(email: string, password: string): Promise<{ user: MockUser }> {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 1000))
    
    // Simple validation
    if (!email || !password) {
      throw new Error('Email and password are required')
    }
    
    if (password.length < 6) {
      throw new Error('Password should be at least 6 characters')
    }

    const user: MockUser = {
      uid: `mock-${Date.now()}`,
      email,
      displayName: email.split('@')[0]
    }

    this.currentUser = user
    
    // Store in localStorage for persistence
    localStorage.setItem('mockUser', JSON.stringify(user))
    
    // Notify listeners immediately
    this.notifyListeners()
    
    return { user }
  }

  async signOut(): Promise<void> {
    await new Promise(resolve => setTimeout(resolve, 500))
    this.currentUser = null
    
    // Remove from localStorage
    localStorage.removeItem('mockUser')
    
    this.notifyListeners()
  }

  onAuthStateChanged(callback: (user: MockUser | null) => void): () => void {
    this.listeners.push(callback)
    
    // Check for existing user in localStorage
    if (!this.currentUser && typeof window !== 'undefined') {
      const storedUser = localStorage.getItem('mockUser')
      if (storedUser) {
        try {
          this.currentUser = JSON.parse(storedUser)
        } catch (error) {
          console.warn('Failed to parse stored user:', error)
          localStorage.removeItem('mockUser')
        }
      }
    }
    
    // Immediately call with current state
    setTimeout(() => callback(this.currentUser), 100)
    
    // Return unsubscribe function
    return () => {
      const index = this.listeners.indexOf(callback)
      if (index > -1) {
        this.listeners.splice(index, 1)
      }
    }
  }

  private notifyListeners() {
    this.listeners.forEach(listener => {
      setTimeout(() => listener(this.currentUser), 0)
    })
  }

  get user() {
    return this.currentUser
  }
}

export const mockAuth = new MockAuth()

// Mock Firestore
class MockFirestore {
  async doc(path: string) {
    return {
      set: async (data: any) => {
        console.log('Mock Firestore: Setting document', path, data)
        // Store in localStorage for persistence
        localStorage.setItem(`firestore_${path}`, JSON.stringify(data))
        return Promise.resolve()
      },
      get: async () => {
        console.log('Mock Firestore: Getting document', path)
        const stored = localStorage.getItem(`firestore_${path}`)
        if (stored) {
          return {
            exists: () => true,
            data: () => JSON.parse(stored)
          }
        }
        return {
          exists: () => true,
          data: () => ({
            fullName: 'Mock User',
            email: 'mock@example.com',
            createdAt: new Date().toISOString()
          })
        }
      }
    }
  }
}

export const mockDb = new MockFirestore()
